import { Button, Snackbar } from '@material-ui/core'
import { SnackbarProps } from '@material-ui/core/Snackbar'
import { serviceWorker } from '@speedlo/tools'
import { formatDistanceStrict } from 'date-fns'
import { useObservable, useObserver } from 'mobx-react-lite'
import React, { useCallback, useRef, useState } from 'react'

import { logMain } from '../helpers/logger'
import { useEffectOnce } from '../hooks/useEffectOnce'
import { HOUR, MINUTE, SECOND, useTimer } from '../hooks/useTimer'
import { i18n } from '../i18n'
import { snackStyles } from './SnackProvider'

export interface IProps {
  onRestart?(): void
  autoUpdateDelay?: number
  postponeTimeout?: number
  updateCheckInterval?: number
  getNow?(): number
  children: ReactNode
}

export const UpdateHandler: React.FC<IProps> = ({
  onRestart = () => window.location.reload(),
  autoUpdateDelay = 20 * SECOND,
  postponeTimeout = 2 * MINUTE,
  updateCheckInterval = process.env.REACT_APP_STAGE === 'prod'
    ? 1 * HOUR
    : 5 * MINUTE,
  getNow = Date.now,
  children,
}) => {
  const updateWorker = useRef<ServiceWorker | null>(null)

  const state = useObservable({ updatePending: false, postponedAt: getNow() })

  const onMessage = useCallback((event: MessageEvent) => {
    if (event.data === 'update-applied') {
      logMain('restarting to apply update...')
      onRestart()
    }
  }, [])

  useEffectOnce(() => {
    let checkIntervalRef
    const init = serviceWorker.register({
      onInit: reg => {
        checkIntervalRef = setInterval(() => {
          logMain('checking for updates...')
          reg.update()
        }, updateCheckInterval)
        logMain('update handler initialized')
      },
      onUpdate: sw => {
        updateWorker.current = sw
        state.updatePending = true
        logMain('new update is available')
        window.navigator.serviceWorker.addEventListener('message', onMessage)
      },
    })
    // service worker is normally using load event, but since this
    // done after the load, it has to be triggered manually
    init()
    return () => {
      window.navigator.serviceWorker.removeEventListener('message', onMessage)
      if (checkIntervalRef) {
        clearInterval(checkIntervalRef)
      }
    }
  })

  const onCancel = useCallback(() => {
    logMain('user postponed update')
    state.updatePending = false
    setTimeout(() => {
      logMain('reminding update to user again')
      state.updatePending = true
    }, postponeTimeout)
  }, [])

  const onConfirm = useCallback(() => {
    state.updatePending = false
    if (updateWorker.current) {
      logMain('user confirmed update')
      updateWorker.current.postMessage('apply-update')
    }
  }, [])

  return useObserver(() => (
    <>
      {state.updatePending ? (
        <UpdateSnack
          autoHideDuration={autoUpdateDelay}
          onCancel={onCancel}
          onConfirm={onConfirm}
          getNow={getNow}
        />
      ) : null}
      {children}
    </>
  ))
}

interface IUpdateSnackProps extends Omit<SnackbarProps, 'open'> {
  autoHideDuration: number
  onConfirm(): void
  onCancel(): void
  getNow(): number
}

export function UpdateSnack(props: IUpdateSnackProps) {
  const { onConfirm, onCancel, getNow, ...rest } = props
  const [endTime] = useState(() => getNow() + props.autoHideDuration)
  const now = useTimer({ interval: SECOND, enabled: true, getNow })
  const remainingSeconds = formatDistanceStrict(endTime, now)
  return (
    <Snackbar
      open={true}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'center',
      }}
      classes={{ root: snackStyles.variantInfo }}
      message={
        <span data-testid="message">{i18n.t`Application will be restarted in ${remainingSeconds} to apply available update.`}</span>
      }
      disableWindowBlurListener={true}
      action={[
        <Button
          key="later"
          color="secondary"
          size="small"
          role="button"
          data-testid="postpone-button"
          onClick={onCancel}
        >
          {i18n.t`Postpone update`}
        </Button>,
        <Button
          key="confirm"
          color="primary"
          size="small"
          role="button"
          data-testid="confirm-button"
          onClick={onConfirm}
        >
          {i18n.t`Restart now`}
        </Button>,
      ]}
      onClose={onCancel}
      {...rest}
    />
  )
}
