import moment from 'moment'

export async function changeMomentLocale(lang: string) {
  if (lang === 'en') {
    moment.locale(lang)
    return
  }
  const locale = await import(
    /* webpackMode: "lazy", webpackChunkName: "moment-[index]" */
    `moment/locale/${lang}`
  )
  moment.updateLocale(lang, locale)
}
