import React from 'react'
import { Route, RouteComponentProps, RouteProps } from 'react-router'

import Pacman from '../atoms/Pacman'

interface IProps<Params> extends Omit<RouteProps, 'render' | 'children'> {
  component: React.ComponentType<RouteComponentProps<Params>>
}

export function LazyRoute<Params = {}>({
  component: Component,
  ...props
}: IProps<Params>) {
  return (
    <Route
      {...props}
      render={routeProps => (
        <React.Suspense fallback={<Pacman />}>
          <Component {...routeProps} />
        </React.Suspense>
      )}
    />
  )
}
