import { LocationDescriptor } from 'history'
import numeral from 'numeral'

import { getQueryErrors } from '../../../graph/errorHandler'
import {
  GMenuRecipeCreate,
  GMenuRecipeDefaults,
  GMenuRecipeDelete,
  GMenuRecipeUpdate,
  WareCategoryTypeEnum,
  WarePriceTypeEnum,
} from '../../../graph/types'
import { RM } from '../../../helpers/ramda'
import { TRecipeFormValues } from '../menutypes'
import { MenuRecipeDefaults } from '../organisms/RecipeForm.graphql'
import { MenuRecipeListQuery } from '../organisms/RecipeList.graphql'
import { MenuBaseModel } from './MenuBaseModel'
import {
  MenuRecipeCreateMutation,
  MenuRecipeDeleteMutation,
  MenuRecipeUpdateMutation,
  MenuRecipeVisibilityMutation,
} from './MenuRecipeModel.graphql'

export const MenuRecipeModel = MenuBaseModel.named('MenuRecipe')
  .volatile(() => ({
    categoryFilterId: null as MaybeID,
  }))
  .actions(self => ({
    changeVisibility(recipeId: ID, visible: boolean) {
      self.client.mutate({
        mutation: MenuRecipeVisibilityMutation,
        variables: { recipeId, visible },
        optimisticResponse: {
          updateRecipe: {
            __typename: 'Recipe',
            id: recipeId,
            visible,
          },
        },
      })
    },
    setCategoryFilter(categoryId: MaybeID) {
      self.categoryFilterId = categoryId
    },
    createRecipe(
      formValues: TRecipeFormValues,
      recipeType: WareCategoryTypeEnum,
    ) {
      const variables: GMenuRecipeCreate.Variables = {
        recipe: {
          companyBranch: self.branchId,
          wareCategoryType: recipeType,
          ...getFormVariables(formValues),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuRecipeCreateMutation,
          variables,
          refetchQueries: [
            {
              query: MenuRecipeListQuery,
              variables: {
                branchId: self.branchId,
              },
            },
          ],
        }),
      )
    },
    updateRecipe(formValues: TRecipeFormValues) {
      const variables: GMenuRecipeUpdate.Variables = {
        recipeId: formValues.id,
        recipe: {
          ...getFormVariables(formValues),
        },
      }
      return getQueryErrors(
        self.client.mutate({
          mutation: MenuRecipeUpdateMutation,
          variables,
        }),
      )
    },
    async deleteRecipe(recipeId: ID) {
      const variables: GMenuRecipeDelete.Variables = {
        recipeId,
      }
      await self.client.mutate({
        mutation: MenuRecipeDeleteMutation,
        variables,
        refetchQueries: [
          {
            query: MenuRecipeListQuery,
            variables: {
              branchId: self.branchId,
            },
          },
        ],
      })
    },
    async queryDefaults() {
      const { data } = await self.client.query<GMenuRecipeDefaults.Query>({
        query: MenuRecipeDefaults,
        variables: { branchId: self.branchId },
      })
      const {
        companyBranch: {
          vats,
          currency: { code },
        },
      } = data
      return {
        vats: vats.reduce((result, { vatId, priceType }) => {
          result[priceType.enum] = vatId
          return result
        }, {}),
        currencyCode: code,
      }
    },
  }))

function getFormVariables(formValues: TRecipeFormValues) {
  return {
    code: formValues.code,
    nameLabel: formValues.nameLabel,
    internalNote: formValues.internalNote,
    descriptionLabel: formValues.descriptionLabel,
    enabled: formValues.visible,
    prices: Object.keys(formValues.prices).map(priceType => ({
      warePriceType: (priceType as unknown) as WarePriceTypeEnum,
      price: { value: numeral(formValues.prices[priceType].value).value() },
      vat: formValues.prices[priceType].vatId!,
    })),
    sideDishCategories: formValues.sideDishes.map(RM.prop('id')),
    wareCategories: formValues.categories.map(RM.prop('id')),
    associatedRecipes: formValues.associatedRecipes.map(
      RM.pick(['associatedRecipe', 'warePriceType']),
    ),
    ingredients: formValues.ingredients.map(({ stockItem, quantity }) => {
      return { stockItem, quantity: parseNumber(quantity) }
    }),
  }
}

function parseNumber(value: string | number) {
  return typeof value === 'number'
    ? value
    : Number.parseFloat(value.replace(',', '.'))
}

export const getRecipeRoute = (
  recipeId: ID,
  recipeType: WareCategoryTypeEnum,
): LocationDescriptor => ({
  pathname: recipeId,
  state: { recipeType },
})

export type TMenuRecipeModel = typeof MenuRecipeModel.Type
