import { css } from 'emotion'
import { SnackbarProvider } from 'notistack'
import React from 'react'

import { Colors, Spacing } from '../styling'

interface IProps {}

const successStyle = css`
  background: ${Colors.green} !important;
  font-size: ${Spacing.InfoMessageFontSize} !important;
`
const errorStyle = css`
  background: ${Colors.red};
  font-size: ${Spacing.InfoMessageFontSize} !important;
`
const warningStyle = css`
  background: ${Colors.orange};
  font-size: ${Spacing.InfoMessageFontSize} !important;
`
export const snackStyles = {
  variantSuccess: successStyle,
  variantInfo: successStyle,
  variantError: errorStyle,
  variantWarning: warningStyle,
}

export const SnackProvider: React.FC<IProps> = ({ children }) => (
  <SnackbarProvider
    maxSnack={3}
    classes={snackStyles}
    anchorOrigin={{ horizontal: 'center', vertical: 'top' }}
  >
    {children}
  </SnackbarProvider>
)
