import { Trans } from '@lingui/react'
import { Button, Snackbar, SnackbarContent } from '@material-ui/core'
import { useAsyncEffect } from '@speedlo/hooks'
import { NormalizedCacheObject } from 'apollo-cache-inmemory'
import { ApolloClient } from 'apollo-client'
import { Observer } from 'mobx-react'
import React, { Children, useContext, useState } from 'react'
import { ApolloProvider } from 'react-apollo'
import { ApolloProvider as ApolloProviderHooked } from 'react-apollo-hooks'

import { RootContext } from '../context/RootContext'
import { createApolloClient } from '../graph/createClient'
import { persistModel } from '../helpers/persistModel'
import { AuthModel } from '../models/AuthModel'
import { RootModel, TRootModel } from '../models/RootModel'
import Pacman from './Pacman'
import { snackStyles } from './SnackProvider'

export const RootProvider: React.FC = ({ children }) => {
  const [root, setRoot] = useState<TRootModel | undefined>(undefined)
  const [hasNetworkError, setNetworkError] = useState(false)

  useAsyncEffect(async () => {
    const auth = AuthModel.create({})
    const apolloClient = createApolloClient(auth)

    const rootModel = RootModel.create(
      { auth },
      {
        fetch: window.fetch,
        apolloClient,
      },
    )

    await Promise.all(rootModel.persistedModels.map(persistModel))

    try {
      await rootModel.user.initUser()
    } catch (err) {
      if (err.networkError) {
        setNetworkError(true)
      }
    }

    setRoot(rootModel)
  })

  if (hasNetworkError) {
    return (
      <Snackbar
        anchorOrigin={{ horizontal: 'center', vertical: 'top' }}
        autoHideDuration={Infinity}
        open={true}
      >
        <SnackbarContent
          className={snackStyles.variantError}
          message={
            <Trans>
              Communication with server has failed. Please retry the operation
              in a moment again.
            </Trans>
          }
          action={[
            <Button
              key="restart"
              onClick={() => location.reload()}
              color="secondary"
              size="small"
              role="button"
            >
              <Trans>Restart application</Trans>
            </Button>,
          ]}
        />
      </Snackbar>
    )
  }

  return root ? (
    <ApolloProvider client={root.client}>
      <ApolloProviderHooked client={root.client}>
        <RootContext.Provider value={root}>
          {Children.only(children)}
        </RootContext.Provider>
      </ApolloProviderHooked>
    </ApolloProvider>
  ) : (
    <Pacman />
  )
}

export const useRoot = () => {
  const root = useContext(RootContext)
  if (!root) {
    throw new Error('RootProvider missing in the tree')
  }
  return root
}

interface IWithRootProps {
  children(root: TRootModel): ReactNode
  observe?: boolean
}

export const WithRoot: React.FC<IWithRootProps> = ({
  children,
  observe = true,
}) => {
  const root = useRoot()
  return observe === true ? (
    <Observer>{() => children(root)}</Observer>
  ) : (
    children(root)
  )
}

export type RootApolloClient = ApolloClient<NormalizedCacheObject>

export interface IRootEnvironment {
  fetch: TFetch
  apolloClient: RootApolloClient
}
