import './enableDebug'

import React from 'react'
import { render } from 'react-dom'

import { App } from './App'
import { RootProvider } from './atoms/RootProvider'
import { Router } from './atoms/Router'
import { ThemeProvider } from './atoms/ThemeProvider'
import { UpdateHandler } from './atoms/UpdateHandler'
import { installSentry } from './helpers/sentry'
import { I18nManager } from './i18n'

const Main = () => (
  <ThemeProvider>
    <RootProvider>
      <I18nManager>
        <UpdateHandler>
          <Router>
            <App />
          </Router>
        </UpdateHandler>
      </I18nManager>
    </RootProvider>
  </ThemeProvider>
)

installSentry(() => {
  const rootElement = document.getElementById('root')
  render(<Main />, rootElement)
})
