import _ from 'lodash'
import { autorun } from 'mobx'
import { flow, Instance, SnapshotIn, types } from 'mobx-state-tree'
import * as R from 'ramda'

import { GCompanyBranch, GUserModelBootstrap } from '../graph/types'
import { logBootstrap, logLang } from '../helpers/logger'
import { RM } from '../helpers/ramda'
import { changeLanguage, detectLanguage } from '../i18n'
import { BaseModel } from '../models/BaseModel'
import { UserModelBootstrapQuery } from './UserModel.graphql'

interface IVolatileProps {
  isLoaded: boolean
  branches: ReadonlyArray<GCompanyBranch.Fragment>
  brands: ReadonlyArray<GCompanyBranch.Brand>
  soundEnabled: boolean
  access: Maybe<IUserAccess>
  permissions: GUserModelBootstrap.UserPermissions
}

export const UserModel = BaseModel.named('User')
  .props({
    language: types.maybe(types.string),
    soundVolume: types.optional(types.number, 25),
    soundIgnore: false,
  })
  .volatile<IVolatileProps>(() => ({
    isLoaded: false,
    branches: [],
    brands: [],
    soundEnabled: false,
    access: null,
    permissions: {
      canCancelOrders: false,
      canCreateOrder: false,
    },
  }))
  .views(self => ({
    get branchesId() {
      return self.branches.map(R.prop('id'))
    },
    get defaultBranch() {
      if (self.branches.length === 0) {
        throw new Error('No user branches available to get defaultBranchId')
      }
      return self.branches[0]
    },
    filterBranches(predicate: (value: GCompanyBranch.Fragment) => boolean) {
      return self.branches.filter(predicate)
    },
    canQuery(queryName: string) {
      return (
        self.access !== null && self.access.queries.has(queryName.toLowerCase())
      )
    },
    canMutate(mutationName: string) {
      return (
        self.access !== null &&
        self.access.mutations.has(mutationName.toLowerCase())
      )
    },
    get isReady() {
      return self.isLoaded || !self.root.auth.isAuthenticated
    },
  }))
  .actions(self => ({
    applyUserData(user: GUserModelBootstrap.User) {
      self.branches = user.branches
      self.brands = RM.uniqBy(
        RM.prop('id'),
        user.branches.map(RM.prop('brand')),
      )
    },
    applyUserPermissions(permissions: GUserModelBootstrap.UserPermissions) {
      self.permissions = permissions
    },
    applyUserAccess(access: IAccessRaw) {
      self.access = _.mapValues(
        access,
        ({ fields }) =>
          new Set(
            fields.map(
              RM.compose(
                RM.toLower,
                RM.prop('name'),
              ),
            ),
          ),
      )
    },
    setLanguage: flow(function*(language: string) {
      logLang('changing to language %s from user profile', language)
      yield changeLanguage(language)
      self.language = language
    }),
    finishBootstrap() {
      self.isLoaded = true
    },
    controlSound(enabled: boolean = true, ignore: boolean = false) {
      self.soundEnabled = enabled
      self.soundIgnore = ignore
    },
    setSoundVolume(volume: number) {
      self.soundVolume = volume
    },
  }))
  .actions(self => {
    const queryUser = async (userId: ID) => {
      const variables: GUserModelBootstrap.Variables = {
        userId,
      }
      const { data } = await self.client.query<GUserModelBootstrap.Query>({
        query: UserModelBootstrapQuery,
        variables,
      })
      self.setLanguage(data.user.language.rawCode)
      self.applyUserData(data.user)
      // @ts-ignore https://github.com/dotansimha/graphql-code-generator/issues/643
      self.applyUserAccess(data.userAccess)
      self.applyUserPermissions(data.userPermissions)
      self.finishBootstrap()
    }

    const initUser = () => {
      return new Promise(resolve => {
        autorun(() => {
          if (self.root.auth.userId) {
            logBootstrap('query user')
            if (!self.language) {
              self.setLanguage(detectLanguage())
            }
            resolve(queryUser(self.root.auth.userId))
          } else {
            logBootstrap('user not authenticated')
            self.setLanguage(detectLanguage())
            resolve()
          }
        })
      })
    }

    return { initUser }
  })

interface IUserAccess {
  queries: Set<string>
  mutations: Set<string>
  subscriptions: Set<string>
}

interface IAccessType {
  fields: ReadonlyArray<{ name: string }>
}

interface IAccessRaw {
  queries: IAccessType
  mutations: IAccessType
  subscriptions: IAccessType
}

export interface TUserModel extends Instance<typeof UserModel> {}
export interface TUserModelProps extends SnapshotIn<typeof UserModel> {}
