import gql from 'graphql-tag'

import { GeoPointFragment } from './GeoPointFragment.graphql'

export const CompanyBranchFragment = gql`
  fragment GCompanyBranch on CompanyBranch {
    id
    name
    address {
      country {
        phonePrefix
      }
    }
    gps {
      ...GGeoPoint
    }
    brand {
      id
      name
    }
  }
  ${GeoPointFragment}
`
