import { GOrderBranchLocation, GOrderLocation } from '../../../graph/types'
import { gpsToLatLng } from '../../../helpers/gps'
import { MapModel } from '../../../models/MapModel'
import {
  OrderBranchLocationQuery,
  OrderLocationQuery,
} from './OrderMapModel.graphql'

interface IVolatileProps {
  activeDriverId: MaybeID
}

export const OrderMapModel = MapModel.named('OrderMap')
  .volatile<IVolatileProps>(() => ({
    activeDriverId: null,
  }))
  .actions(self => ({
    async panToOrder(orderId: ID) {
      if (!self.mapRef) {
        return
      }
      const { data } = await self.client.query<GOrderLocation.Query>({
        query: OrderLocationQuery,
        variables: { orderId },
      })
      if (data && data.order.gps) {
        self.mapRef.panTo(gpsToLatLng(data.order.gps))
      }
    },
    async panToBranch(branchId: ID) {
      const { data } = await self.client.query<GOrderBranchLocation.Query>({
        query: OrderBranchLocationQuery,
        variables: { branchId },
      })
      if (!self.mapRef) {
        return
      }
      if (data) {
        self.mapRef.panTo(gpsToLatLng(data.companyBranch.gps))
      }
    },
    activateDriver(driverId: ID, position: GeoPoint) {
      self.activeDriverId = driverId
      if (self.mapRef) {
        self.mapRef.panTo(gpsToLatLng(position))
      }
    },
  }))

export type TOrderMapModel = typeof OrderMapModel.Type
